import { Injectable} from '@angular/core';
import * as moment from 'moment';
declare const UPCHAT:any;
declare const BMap: any;
declare const AMap: any;
/**
 * 第三方方法引用
 */
@Injectable()
export class ThirdPath{

  moment:any = moment;

  UPCHAT:any;

  BMap: any;

  AMap: any;

  constructor() {
    try{
      this.UPCHAT = UPCHAT;
      this.BMap = BMap;
      this.AMap = AMap;
    }
    catch(e){

    }

  }


}



