import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Contents} from "../../providers/Constants";
import { HttpService } from "../../providers/HttpService";

/**
 * Generated class for the PotentialcusteditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-potentialcustedit',
  templateUrl: 'potentialcustedit.html',
})
export class PotentialcusteditPage {
  msgDatasList:any
  isEdit:any

  custName:any = ''
  custPhoneNum:any = ''
  companyName:any = ''
  dutyCd:any = ''
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private httpServer:HttpService) {
     this.navParams.get("detail")
    this.isEdit = this.navParams.get('isEdit')
  }
  ionViewDidLoad() {
      this.msgDatasList = this.navParams.get("detail")
    console.log(this.msgDatasList)


  }
  // 添加潜在客户
  addinfo () {
    this.httpServer.postData({
      url:Contents.HTTP_LOCAL_ORIGIN,
      body:{
        "appId":"mcrm",
        "reqParam":{
          "custName":this.custName,
          "mobile": this.custPhoneNum,
          "dutyCd":this.dutyCd,
          "companyName":this.companyName
        },
        "serviceId":"cust.mobile.potenCust.addPotenCustInfo"
      }
    })
      .then(res=>{
        console.log(res)
      }).catch(err=>{
        console.log(err)
    })
  }

  //修改潜在客户信息
  back () {
   console.log(this.msgDatasList)
    this.httpServer.postData({
      url:Contents.HTTP_LOCAL_ORIGIN,
      body:{
        "appId":"mcrm",
        "reqParam":{
          "changejson":"ceshi",
          "custId":"",
          "sex": "F"

        },
        "serviceId":"cust.mobile.potenCust.updata360CustPotenCustInfo"
      }
    })
      .then(res=>{
        console.log(res)
      }).catch(err=>{
      console.log(err)
    })
  }
}



