import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams,ViewController } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";
import {OtherDetailPage} from "../otherdetailpage/otherdetailpage";
import { AboutPage } from '../about/about';

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addplanpage',
  templateUrl: 'addplanpage.html',
})
export class AddplanpagePage {
  addOthers:any={};
  addParams:any={};
  others:any={};
  infoType:string;
  num:number;
  isDisabled:boolean=true;
  date:any;
  activityType2:any;
  dayTime:string;
  userId:string;
  isSearch:string;
  isAddress:string;
  searchLists:any=[];
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
   public httpService:HttpService,public alertCtrl: AlertController,
   public viewCtrl: ViewController) {
   this.infoType=this.navParams.get('type');
   this.num=this.navParams.get('num');

   console.log(navParams.get('otherDetails'));
	//获取userId
  	this.userId=sessionStorage.getItem("userName");
  	this.isSearch="none";
  	this.isAddress="none";
  	
   if(this.navParams.get('otherDetails')){
   	this.addOthers=this.navParams.get('otherDetails');
   	this.others=JSON.stringify(this.navParams.get('otherDetails'));
   	console.log(this.addOthers);
   	console.log(this.addOthers.time);
   	console.log(this.addOthers.time.substr(0,10));
   	this.addOthers.time=this.addOthers.time.substr(0,10)
   }

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad AddOtherPage');
  }

  transformData(data){
	const da=new Date(data).getDay();
  	switch (da){
  		case 0:
  			this.date="周日";
  			break;
  		case 1:
  			this.date="周一";
  			break;
  		case 2:
  			this.date="周二";
  			break;
  		case 3:
  			this.date="周三";
  			break;
  		case 4:
  			this.date="周四";
  			break;
  		case 5:
  			this.date="周五";
  			break;
  		case 6:
  			this.date="周六";
  			break;
  		default:
  			break;
  	}
  	return (data+" "+this.date);
  }
  saveInfo(isSubmit){
  	console.log(this.addOthers.time);
  	const date=new Date(this.addOthers.time);
  	this.dayTime=date.getFullYear()+"年"+(date.getMonth()+1)+"月"+date.getDate()+"日 "+date.getHours()+":"+date.getMinutes();

  	const URL="search/addPlan.json";
  	const par={
  		"customName":this.addOthers.title,
  		"planDate":this.dayTime,
  		"province":this.addOthers.province,
  		"city":this.addOthers.area,
  		"isSubmit":isSubmit,
  		"userId":this.userId,
  		"isAdd":0
  	};
  	this.httpService.getData({
        url: URL,
        body: par,
    }).then(res => {
    	console.log(res);
      let result = res.returnCode;
      if (result == 0) {
        this.showAlertMsg("保存成功",true);
      }
      else {
        this.showAlertMsg( "保存失败",false);
      }
    }).catch(error => {
        this.showAlertMsg(error,false);
    })
  }
  showAlertMsg(messag,isTouter) {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: messag,
      buttons: [
      	{
      		text:"确定",
      		handler: () => {
		      console.log('Cancel clicked');

		     if(isTouter){
		     	if(this.infoType=="编辑"){
					this.dismiss(true);
			    }else{
					this.dismiss(true);
				}
		     }

		    }
      	}
      ]

    });
    alert.present();
  }
  dismiss(data) {
  	if(data){
  		this.viewCtrl.dismiss(this.addOthers);
  	}else{
  		console.log(this.others);
//		this.viewCtrl.dismiss(JSON.parse(this.others));
	 	if(this.infoType!="编辑其它"){
	 		this.addOthers=this.others;
	 		this.viewCtrl.dismiss(this.addOthers);
	    }else{
	    	this.addOthers=JSON.parse(this.others);
	    	this.viewCtrl.dismiss(this.addOthers);
	    }

  	}

  }


   //点击选择省份
  switchType(){
    this.activityType2="请选择市区";
//  console.log(this.meterial);
    console.log('选择类型');
    this.activityType2="请选择市区";
  }
    //点击选择市
  switchType2(){
//  console.log(this.meterial);
    console.log('选择类型');
  }
  
  goSearch(){
   	const url = "search/customer.json";
   	const param={
   		"customName":this.addOthers.title,
      	"pageNo":"1",
      	"dataCount":"10",
      	"fisrPageNo":"10",
      	"vague":true   
   	}
    this.httpService.getData({
      url: url,
      body: param
    })
    .then(res=>{
      console.log(res);
      this.searchLists=res.customs;
      if(this.searchLists.length==0){
      	this.isSearch='none';
      }else{
      	this.isSearch='block';
      }
    })
   }
  goCustomer(cust){
  	this.addOthers.title=cust.customName;
  	this.isSearch='none';
  }
  addAttendees(){
	this.isAddress="block";
  }

}



