import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PotentialcusteditPage } from "../potentialcustedit/potentialcustedit";
import {Contents} from "../../providers/Constants";
import { HttpService } from "../../providers/HttpService";
/**
 * Generated class for the PotentialcustdetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-potentialcustdetails',
  templateUrl: 'potentialcustdetails.html',
})
export class PotentialcustdetailsPage {
  custDetailList:any = [];
  pkId:string = '';
  dataList:any = []
  isEdit:any
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private httpServer:HttpService) {
    this.pkId = this.navParams.get("detail")
  }
  ionViewDidLoad() {
   this.getpotentData()
  }
  getpotentData () {
    this.httpServer.postData({
      url:Contents.HTTP_LOCAL_ORIGIN,
      body:{
        "appId":"mcrm",
        "reqParam":{
          "pkId":this.pkId
        },
        "serviceId":"cust.mobile.potenCust.queryPotenCustBaseInfo"
      }
    })
      .then(res=>{
        this.custDetailList = []
          this.custDetailList.push(JSON.parse(res.resultRes).data)
      })
  }
  goEditPage (arg) {
    this.navCtrl.push(PotentialcusteditPage,{
      callback:this.CallbackFunction,
      detail:this.custDetailList,
      isEdit:arg
    })
  }
  CallbackFunction = (_params,_params1) => {
    return new Promise((resolve, reject) => {
      this.dataList = _params;
      resolve();
    });
  }
}



