import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MyapprovedetilPage } from "../myapprovedetil/myapprovedetil";

/**
 * Generated class for the MyapprovePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-myapprove',
  templateUrl: 'myapprove.html',
})
export class MyapprovePage {
  pet:any = "kittens"
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyapprovePage');
  }
  gomyapprodetil (args) {
    this.navCtrl.push(MyapprovedetilPage,{
      title:args
    },{
      animate:true,
    })
  }
}



