import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { VisidetailPage } from '../visidetail/visidetail';
import {HttpService} from "../../providers/HttpService";
import { addreportPage } from '../addreport/addreport';
import {ToastController} from 'ionic-angular';

/**
 * Generated class for the VisitlistPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-visitlist',
  templateUrl: 'visitlist.html',
})
export class VisitlistPage {

  //申明list集合
  visilist:any=[];
  //申明员工ID
  empId:any = "1";
  visitPlans:any=[];

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService,public toastCtrl:ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VisitlistPage');
    this.getvisitlistPage();
  }

  //点击进入走访详情页面
  go_visidetail(status){
      // let index;
      // if(status == '待走访'){
      //   index = 1;
      // }else if(status == '已走访'){
      //   index = 2;
      // }else if(status == '待审批'){
      //   index = 3;
      // }else if(status == '已退回'){
      //   index = 4;
      // }
      // this.navCtrl.push(VisidetailPage,{index:index},
      //   { animate: true });

    this.navCtrl.push(addreportPage,
      { animate: true });
  }

  //时间字段格式化
  timeFormat(val) {
    return val.slice(0,10);
  }

  //查询主界面的接口
  getvisitlistPage(){
    const url = "visilist/visilist.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "userId":this.empId,
        "beginDate":"",
        "endDate":"",
        "pageNo":"",
        "firstPageNo":"",
        "dataCount":"",
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      console.log(res.resultRes.visitPlans);
      this.visitPlans = res.resultRes.visitPlans;
      this.visilist =res.resultRes.visitPlans.slice(0,11);
      console.log(this.visilist);
    })

  }

  doInfinite(infiniteScroll) {
    console.log('Begin async operation');

    setTimeout(() => {
      for (let i = 11, len = this.visitPlans.length; i < len; i++) {
        if(this.visilist.length < this.visitPlans.length){
        this.visilist.push( this.visitPlans[i] );
        }else {
          let toast = this.toastCtrl.create({
            message: '已无更多数据',//显示消息
            duration: 3000,//3秒后自动消失
            position: 'bottom'//位置
          })
          toast.present();
          break;
        }
      }

      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 500);
  }


  getWeekday(time){
    const arr = ["周日","周一","周二","周三","周四","周五","周六"];
    return arr[new Date(time).getDay()];
  }

}



