import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { ProjectDetailPage } from '../projectDetail/projectDetail';
import {HttpService} from "../../providers/HttpService";
// import { Cookie } from 'ng2-cookies/ng2-cookies';

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-projectpage',
  templateUrl: 'project.html',
})
export class ProjectPage {
  search:any={};
  projectLists:any=[];
  userName:string;

  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {
  	this.userName=sessionStorage.getItem('userName');
  }

   goCustomer(instId){
   	console.log(instId);
   	console.log('去客户详情');
   	this.navCtrl.push(ProjectDetailPage,{"instId":instId},{ animate: true });
   }
   goSearch(){
   	console.log('搜索');
   	const url = "search/project.json";
   	const param={
   		"customName":this.search.project,
      	"userId":this.userName,
      	"pageNo":"1",
      	"dataCount":"10",
      	"firstPageNo":"10"
   	}
    this.httpService.getData({
      url: url,
      body: param
    })
    .then(res=>{
      console.log(res);
      this.projectLists=res.projects;
    })
   }
   presentModal(){
   	console.log("分享");
   }

}



