import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {CustomerlistPage} from "../customerlist/customerlist";
import {existingDeepLinkConfigString} from "@ionic/app-scripts/dist/deep-linking";


/**
 * Generated class for the VisitPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-visit',
  templateUrl: 'visit.html',
})
export class VisitPage {
  visitgetfolist:any=[];
  visitgetfolistiph:any=[];
  newtime:any={};
  vame:boolean= true;
  opTionValue : any = sessionStorage.getItem("id");
  valueList : any = [
    {"value":"1","name":"正式客户"},
    {"value":"2","name":"潜在客户"},
    {"value":"3","name":"非管户客户"}
  ];


  constructor(public navCtrl: NavController,
              public navParams: NavParams
  ) {
    this.visitgetfolist.push(this.navParams.get('detail'));
    this.visitgetfolistiph.push(this.navParams.get('iph'));
    this.newtime.starttime = new Date(new Date().getTime()+8*60*60*1000).toISOString();
  }

  ionViewDidLoad() {
    if(this.opTionValue == "" || this.opTionValue == null){
      this.opTionValue = "1";
    }
    console.log(this.navParams.get('iph'));

  }

  change(opTionValue){
    console.log(opTionValue);
    if(opTionValue=="3"){
      this.vame=false
    }else{
      this.vame=true
    }

    this.vame=false
    this.opTionValue=opTionValue;
    sessionStorage.setItem("id",this.opTionValue);
    // window.location.reload()
  }

changebutton(){
    this.vame=true
  this.navCtrl.push(CustomerlistPage,
    {
      callback:this.CallbackFunction,
      animate: true });
}

  //回调方法
  CallbackFunction = (_params,_params1) => {
    return new Promise((resolve, reject) => {
      this.visitgetfolist = _params;
      this.visitgetfolistiph = _params1;
      resolve();
    });
  }
}



