import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { ExaminerecordPage } from "../examinerecord/examinerecord";

/**
 * Generated class for the MyapprovedetilPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-myapprovedetil',
  templateUrl: 'myapprovedetil.html',
})
export class MyapprovedetilPage {


  ionViewDidLoad() {
    console.log('ionViewDidLoad MyapprovedetilPage');
  }
  headerText:any
  isAgree:any = 0
  isDisagree:any = ""
  rejectCont:any = ""
  isdispose:any = 0

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public alertCtrl: AlertController) {
    this.headerText = this.navParams.get("title")
  }

  agree (args) {
    let confirm  = this.alertCtrl.create({
      message: '请输入同意意见',
      inputs: [
        {
          name: 'cont',
          placeholder: '请输入您的意见'
        },
      ],
      buttons: [
        {
          text: '返回',
          handler: data => {
          }
        },
        {
          text: '确定',
          handler: data => {
            console.log(data.cont+"同意意见")
            this.rejectCont = data
          }
        }
      ]
    });
    confirm.present()
  }




  doPrompt() {
    let prompt = this.alertCtrl.create({
      message: "请输入拒绝建议",
      inputs: [
        {
          name: 'cont',
          placeholder: '请输入您的意见'
        },
      ],
      buttons: [
        {
          text: '返回',
          handler: data => {
          }
        },
        {
          text: '确定',
          handler: data => {
            console.log(data.cont+"拒绝意见")
            this.isDisagree = data
          }
        }
      ]
    });
    prompt.present();
  }

  goExamineRecord () {
    this.navCtrl.push(ExaminerecordPage)
  }
}



