import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { BalanceDetailPage } from '../balanceDetail/balanceDetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-balancepage',
  templateUrl: 'balance.html',
})
export class BalancePage {
  balance:any={};
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {}

   goRateDetail(search){
   	console.log('去客户详情');
   	const url = "search/balance.json";
	   	const param={
	   		"name":this.balance.name,
	   		"AccountId":this.balance.AccountId,
	      	"beginDate":this.balance.start,
	      	"endDate":this.balance.end,
	      	"pageNo":"1",
	      	"dataCount":"1",
	      	"firstPageNo":"1"
	   	}
	    this.httpService.getData({
	      url: url,
	      body: param
	    })
	    .then(res=>{
	      console.log(res);
		  this.navCtrl.push(BalanceDetailPage,{"balanceDetails":res},{ animate: true });
	    })
   	
   }
   presentModal(){
   	console.log("分享");
   }

}



