import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PotentialcustdetailsPage } from "../potentialcustdetails/potentialcustdetails";
import { HttpService } from "../../providers/HttpService"
import { Contents } from "../../providers/Constants";
import { PotentialcusteditPage } from "../potentialcustedit/potentialcustedit";

/**
 * Generated class for the PotentialcustPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-potentialcust',
  templateUrl: 'potentialcust.html',
})
export class PotentialcustPage {
  custinfo:any = []
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private httpServer:HttpService) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PotentialcustPage');
    this.getpotentialcustinfo ()
  }
  CallPhone(num,e){
    //alert(phone);
    e.preventDefault()
    window.open('tel:'+num);
  }

  getpotentialcustinfo () {
    this.custinfo = []
    // const url = "custList/potentialcustList.json"
    this.httpServer.postData({
      url:Contents.HTTP_LOCAL_ORIGIN,

      body:{
        "appId":"mcrm",
        "reqParam":{
          // "custName":"",
          // "custId":"",
          // "idenNo":"",
          "page":{
            "pageSize":10,
            "pageIndex":1
          }
        },
        "serviceId":"cust.mobile.potenCust.queryMyPotenCust"
      }
    })
      .then(res=>{
        let infoList = JSON.parse(res.resultRes).data
        console.log(infoList)
        for(let i in infoList){
          this.custinfo.push(infoList[i])

        }
      })
  }

  goDetailPage (iteminfo) {
    // this.navCtrl.push(PotentialcustdetailsPage)
    console.log("列表点击事件")
    this.navCtrl.push(PotentialcustdetailsPage,{
      detail:iteminfo
    },{
      animate: true
    })
  }
  goaddPage (arg) {
    this.navCtrl.push(PotentialcusteditPage,{
      isEdit: arg
    })
  }
  goTakepicPage () {
  }
}



