import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams,ViewController } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";
import {OtherDetailPage} from "../otherdetailpage/otherdetailpage";
import { AboutPage } from '../about/about';

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addotherpage',
  templateUrl: 'addotherpage.html',
})
export class AddOtherPage {
  addOthers:any={};
  addParams:any={};
  others:any={};
  infoType:string;
  num:number;
  isDisabled:boolean=true;
  date:any;
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
   public httpService:HttpService,public alertCtrl: AlertController,
   public viewCtrl: ViewController) {
   this.infoType=this.navParams.get('type');
   this.num=this.navParams.get('num');

   console.log(navParams.get('otherDetails'));

   if(this.navParams.get('otherDetails')){
   	this.addOthers=this.navParams.get('otherDetails');
   	this.others=JSON.stringify(this.navParams.get('otherDetails'));
   	console.log(this.addOthers);
   	console.log(this.addOthers.time);
   	console.log(this.addOthers.time.substr(0,10));
   	this.addOthers.time=this.addOthers.time.substr(0,10)
   }
  
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad AddOtherPage');
  }
  
  saveOther(){
//	if(this.infoType!="编辑其它"){
// 	this.addOthers.style="计划"+(this.num+1);
// }
//	const date=new Date();

	
//	this.addOthers.nowTime=date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" "+date.getHours()+":"+date.getMinutes();
  	console.log(this.addOthers);
  	this.addOthers.time=this.transformData(this.addOthers.time);
  	if(this.infoType=="编辑其它"){
  		//编辑存数据库url
//		const url = "performance/customerCurrent.json";
		const url = "calendar/editOther.json";
		this.addParams={
			"appId":"1",
			"serviceId":"1111",
			"reqParam":{
				"pkId":"11",
				"bizCode":"12",
				"bizVal":"13",
				"title":this.addOthers.title,
				"time":this.addOthers.time,
				"Content":this.addOthers.content
			}
		}
  		this.saveInfo(url);
  	}else{
  		//添加存数据库url
  		if(Object.keys(this.addOthers).length!=3){
			return;
		}
//		const url = "performance/customerCurrent.json";
		const url = "calendar/addOther.json";
		this.addParams={
			"appId":"1",
			"serviceId":"1111",
			"reqParam":{
				"title":this.addOthers.title,
				"time":this.addOthers.time,
				"Content":this.addOthers.content
			}
		}
  		this.saveInfo(url);
  	}
  	
	
  	
  }
  transformData(data){
	const da=new Date(data).getDay();
  	switch (da){
  		case 0:
  			this.date="周日";
  			break;
  		case 1:
  			this.date="周一";
  			break;
  		case 2:
  			this.date="周二";
  			break;
  		case 3:
  			this.date="周三";
  			break;
  		case 4:
  			this.date="周四";
  			break;
  		case 5:
  			this.date="周五";
  			break;
  		case 6:
  			this.date="周六";
  			break;
  		default:
  			break;
  	}
  	return (data+" "+this.date);
  }
  saveInfo(URL){
  	this.httpService.getData({
        url: URL,
        body: {
//        params: this.addOthers
          params: this.addParams
        },
    }).then(res => {
    	console.log(res);
      let result = res.status;
      if (result == 0) {
        this.showAlertMsg("保存成功",true);
      }
      else {
        this.showAlertMsg( "保存失败",false);
      }
    }).catch(error => {
        this.showAlertMsg(error,false);
    })
  }
  showAlertMsg(messag,isTouter) {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: messag,
      buttons: [
      	{
      		text:"确定",
      		handler: () => {
		      console.log('Cancel clicked');

		     if(isTouter){
		     	if(this.infoType=="编辑"){
					this.dismiss(true);
			    }else{
					this.dismiss(true);
				}
		     }
		      
		    }
      	}
      ]
   	  
    });
    alert.present();
  }
  dismiss(data) {
  	if(data){
  		this.viewCtrl.dismiss(this.addOthers);
  	}else{
  		console.log(this.others);
//		this.viewCtrl.dismiss(JSON.parse(this.others));
	 	if(this.infoType!="编辑其它"){
	 		this.addOthers=this.others;
	 		this.viewCtrl.dismiss(this.addOthers);
	    }else{
	    	this.addOthers=JSON.parse(this.others);	 
	    	this.viewCtrl.dismiss(this.addOthers);
	    }
	    
  	}
	
  }

}



