import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {PeoplereceptPage} from "../peoplerecept/peoplerecept";
/**
 * Generated class for the ReceptionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reception',
  templateUrl: 'reception.html',
})
export class ReceptionPage {
  visitgetfolist:any=[];
  visitgetfolistiph:any=[];
  newtime:any={};
  opTionValue : any = sessionStorage.getItem("id");
  valueList : any = [
    {"value":"1","name":"正式客户"},
    {"value":"2","name":"潜在客户"}
  ];


  constructor(public navCtrl: NavController,
              public navParams: NavParams
  ) {
    this.visitgetfolist.push(this.navParams.get('detail'));
    this.visitgetfolistiph.push(this.navParams.get('iph'));
    this.newtime.starttime = new Date(new Date().getTime()+8*60*60*1000).toISOString();
  }

  ionViewDidLoad() {
    if(this.opTionValue == "" || this.opTionValue == null){
      this.opTionValue = "1";
    }
    console.log(this.visitgetfolist);
  }

  change(opTionValue){
    this.opTionValue=opTionValue;
    sessionStorage.setItem("id",this.opTionValue);
    // window.location.reload()
  }

  changebutton(){
    this.navCtrl.push(PeoplereceptPage,
      {
        callback:this.CallbackFunction,
        animate: true
      });
  }

  //回调方法
  CallbackFunction = (_params,_params1) => {
    return new Promise((resolve, reject) => {
      this.visitgetfolist = _params;
      this.visitgetfolistiph = _params1;
      resolve();
    });
  }
}



