import { Component ,ChangeDetectorRef,ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams,Content } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";
import { CustomermanagerlistPage } from '../customermanagerlist/customermanagerlist';

/**
 * Generated class for the CustomermanagerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

declare var echarts;//js echarts全局对象
@IonicPage(
  {
    name: 'CustomermanagerPage',
  })

@Component({
  selector: 'page-customermanager',
  templateUrl: 'customermanager.html',
})

export class CustomermanagerPage {

  myChartpie:any;
  myChartbar:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService,public cd : ChangeDetectorRef,) {

  }

  @ViewChild(Content) content: Content;
  ionViewDidLoad() {
    let navtest = this.navCtrl;
    this.myChartpie =echarts.init(document.getElementById('pie') as HTMLDivElement);
    this.myChartpie.on('click', function (params) {
      console.log("您点击的类别是："+params.data.name);//从params集合中获取状态state
      navtest.push(CustomermanagerlistPage,{},
        { animate: true });
    })

    console.log('ionViewDidLoad CustomermanagerPage');
    const url = "performance/customerCurrent.json";
    this.httpService.postData({
      url: url,
      body: {
      }
    }).then(res=>{
      let pieData = res.resultRes.targetSummary;
      let datapie = [];
      //放置左边标签，存入名字
      let datalegend = [];
      for(let date of pieData){
        let obj={};
        obj['name'] = date.indicatorType;
        obj['value'] = date.indicatorSum;
        datapie.push(obj);
        datalegend.push(date.indicatorType);
      }
      console.log(datapie);
      console.log(datalegend);


      this.myChartpie.setOption(
        {
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          // legend: {
          //   orient: 'vertical',
          //   left: 'left',
          //   data: datalegend
          // },
          series : [
            {
              name: '',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:datapie,
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        }
      );

      //第二个图表
      this.myChartbar =echarts.init(document.getElementById('bar') as HTMLDivElement);
      this.myChartbar.setOption(
        {
          color: ['#8bc9df'],
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
              type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis : [
            {
              axisLine:{
                show: true,
                lineStyle:{
                  color:'#626262',
                  width:1,//这里是为了突出显示加上的
                }
              },
              axisTick: {
                alignWithLabel: false,
                show: false
              },
              axisLabel: {
                interval:0,//横轴信息全部显示
                rotate:40,//-30度角倾斜显示
                show:true,
                textStyle: {
                  color: '#d3d3d3',
                },
              },
              type : 'category',
              data :  datalegend
            }
          ],
          yAxis : [
            {
              axisLine: {
                show: true,
                color:'#d3d3d3'
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: ['#f3f3f3']
                }
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#d3d3d3',
                },
              },
              type : 'value'
            }
          ],
          series : [
            {
              name:'',
              type:'bar',
              barWidth: '60%',
              data:datapie
            }
          ]
        }
      );
    })
  }


}



