import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { ApprovedpendingPage } from '../approvedpending/approvedpending';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the ApprovewechartdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approvewechartdetail',
  templateUrl: 'approvewechartdetail.html',
})
export class ApprovewechartdetailPage {

  //申明流程ID
  processinstId:any;
  //申明尽调ID
  wechartId:any;
  //申明详情集合
  wechartDetail:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController,public httpService:HttpService) {
    this.processinstId = navParams.get("processinstId");
    this.wechartId = navParams.get("wechartId");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApprovewechartdetailPage');
    this.getDetailPage();
  }

  //点击提交
  submit(){
    //您点击了提交
    let confirm = this.alertCtrl.create({
      title: '提示',
      message: '是否取消该走访计划?',
      buttons: [
        {
          text: '取消',
          handler: () => {
            console.log('您点击了取消');
          }
        },
        {
          text: '是',
          handler: () => {
            //您点击了提交
            this.navCtrl.push(ApprovedpendingPage,{approve_index:"3"},{ animate: true });
            console.log('您点击了是');
          }
        }
      ]
    });
    confirm.present();
  }

  //查询详情页面接口
  getDetailPage(){
    const url = "approveindialist/approveindiaDetail.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "sealId":this.wechartId,
        "processinstId":this.processinstId
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.wechartDetail =res;
      console.log(this.wechartDetail);
    })

  }

}



