import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { CustomerDetailPage } from '../customerdetail/customerdetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customerpage',
  templateUrl: 'customer.html',
})
export class CustomerPage {
  search:any={};
  searchLists:any=[];
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {}

   goCustomer(cust){
   	console.log('去客户详情');
   	this.navCtrl.push(CustomerDetailPage,{"customer":this.search.customer},{ animate: true });
   }
   goSearch(){
   	const url = "search/customer.json";
   	const param={
   		"customName":this.search.customer,
      	"pageNo":"1",
      	"dataCount":"10",
      	"fisrPageNo":"10",
      	"vague":true   
   	}
    this.httpService.getData({
      url: url,
      body: param
    })
    .then(res=>{
      console.log(res);
      this.searchLists=res.customs;
    })
   }
   presentModal(){
   	console.log('分享');
   }

}



