import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CustomermanagerdetailPage } from '../customermanagerdetail/customermanagerdetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the CustomermanagerlistPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customermanagerlist',
  templateUrl: 'customermanagerlist.html',
})
export class CustomermanagerlistPage {

  //申明一个list集合
  custormerManageList:any;
  //申明类型
  summaryC:any;
  //申明客户ID
  customId:any;
  //输入框的值
  searchvalue:any;
  //申明根据客户模糊查询
  customName:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CustomermanagerlistPage');
    this.customName = "";
    this.getListPage();
  }

  //点击某一项的详情
  customerdetail(){
    //把客户ID传到下一个页面
    this.navCtrl.push(CustomermanagerdetailPage,{customId:this.customId},
      { animate: true });
  }

  //根据传过来的一个类型，查询集合
  getListPage(){
    const url = "approveindialist/approveprojectdetail.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "customName":this.customName,
        "propDesc":'',
        "summaryC":this.summaryC,
        "pageNo":"",
        "dataCount":"",
        "firstPageNo":""
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.custormerManageList = res;
      //在返回的结果提取客户ID，这里是自定义的值，因为不知道返回的结构
      this.customId = 1;
      console.log("这是客户列表数据："+this.custormerManageList);
    })

  }

  //点击查询根据客户名称或者客户类型
  search(){
    //获取input的值
    let secrchVal = this.searchvalue;
    console.log("这是搜索框的值："+secrchVal);
    this.customName = secrchVal;
    this.getListPage();
  }





}



