import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the TargetdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-targetdetail',
  templateUrl: 'targetdetail.html',
})
export class TargetdetailPage {
  contenttext:any = "会议签到：会议发起人可以在APP中点击开始会议，会议开始后，与会人员会议信息中的签到功能开启。与会人员采用定位签到功能，在500-1000米（具体精度再定）范围内才可以签到，超过范围不允许签到。结束会议：会议结束后，会议主持人点击结束会议，会议更新为结束状态后，不允许继续签到。主持人可以填写会议报告。取消会议：对于未开始的会议可以取消会议日程，并推送通知参与人员。正在进行的会议不可取消。"
  textmaxleng:any = ""
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }
  custDetailList:any = [
    {title:"目标类型",content:"日计划"},
    {title:"开始时间",content:"2018-5-15"},
    {title:"结束时间",content:"20187-26"},
    {title:"计划执行人",content:"自己"},
    {title:"计划主题",content:"白领融收单30"},
    {title:"计划内容",content:["海关税务局收20","环球中心收10"]}

  ]
  ionViewDidLoad() {
    console.log('ionViewDidLoad TargetdetailPage');
    console.log(this.custDetailList)
    let maxleng = this.contenttext.substr(0,30);
    this.textmaxleng = maxleng

  }


  // this.custDetailList =

}



