import { Component,OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { CustomermanagerprodetailPage } from '../customermanagerprodetail/customermanagerprodetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customerdetail',
  templateUrl: 'customerdetail.html',
})
export class CustomerDetailPage {
  customers:any={};
  customer:string;
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {
  	this.customer=navParams.get('customer');
  }
	
	ngOnInit() {
		const url = "search/customerDetail.json";
	   	const param={
	   		"customName":this.customer,
	      	"pageNo":"1",
	      	"dataCount":"10",
	      	"fisrPageNo":"1",
			"vague":false  
	   	}
	    this.httpService.getData({
	      url: url,
	      body: param
	    })
	    .then(res=>{
	      console.log(res);
	      this.customers=res.customs;
	    })
	}
	goCustomer(){
		this.navCtrl.push(CustomermanagerprodetailPage,{"customId":this.customers.customId},{ animate: true })
	}
	share(){
		console.log('分享');
	}

}



