import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormalparticularsPage } from "../formalparticulars/formalparticulars";
import { HttpService } from "../../providers/HttpService";
import {Contents} from '../../providers/Constants'

/**
 * Generated class for the FormalcustPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-formalcust',
  templateUrl: 'formalcust.html',
})
export class FormalcustPage {
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private httpServer:HttpService) {
  }
  //结果集
  resultDataList:any = [];
  //总记录数
  totalNum:any;
  pageIndex:any = 0
  ionViewDidLoad() {
    console.log('ionViewDidLoad FormalcustPage');
    this.getFomalCustList(this.pageIndex)
  }
  CallPhone(){
    //alert(phone);
    window.open('tel:11111');
  }

  gopartiulars (arg) {
    this.navCtrl.push(FormalparticularsPage,{
      custId:arg
    })
  }

  /**
   * 请求正式客户列表
   */
  getFomalCustList (pageIndex) {
    this.httpServer.postData({
      url:Contents.HTTP_LOCAL_ORIGIN,
      body:{
        "appId":"mcrm",
        "reqParam":{
          "custName":"",
          "custId":"",
          "idenNo":"",
          "page":{
            "pageSize":10,
            "pageIndex":pageIndex
          }
        },
        "serviceId":Contents.QUERY_FORMAL_CUST_LIST
      }
    }).then(res=>{
      let datalist = JSON.parse(res.resultRes).data
      for(let i=0;i<datalist.length;i++){
        this.resultDataList.push(datalist[i])
      }
      console.log(this.resultDataList)
      this.totalNum=JSON.parse(res.resultRes).count;
    }).catch(err=>{
      console.log(err)
    })

  }



  doInfinite(infiniteScroll) {
     this.pageIndex++

    setTimeout(() => {

      this.getFomalCustList(this.pageIndex)
      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 500);
     console.log(this.pageIndex)
  }
  btnisactive = false
  menuActive (e) {
    console.log(e)
   // this.btnisactive = this.btnisactive == false ? true: false
  }
}



