import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the AddmeetinginfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addmeetinginfo',
  templateUrl: 'addmeetinginfo.html',
})
export class AddmeetinginfoPage {

  constructor(public navCtrl: NavController, public httpService:HttpService, public navParams: NavParams,public alertCtrl:AlertController) {
  }

  meetingtitle:any;
  meetingcontent:any;
  writeCon:any;
  wFlag:boolean;
  ionViewDidLoad() {
    //console.log('ionViewDidLoad AddmeetinginfoPage');

    this.writeCon = "编辑";
    this.wFlag = false;

    const url = "calendar/meeting.json";
    this.httpService.postData({
      url: url,
      body: {
      }
    }).then(res=>{
      //console.log(res);
      if(res.status == 0){
        this.meetingcontent = res.resultRes.content;
        this.meetingtitle = res.resultRes.title;
      }else{

      }

    })
  }

  showAlert(msg) {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: msg,
      buttons: ['确定']
    });
    alert.present();
  }

  saveRemark(meetingcontent,meetingtitle){
    if(this.wFlag == false){
      this.wFlag = true;
      this.writeCon="保存";
      document.getElementById("meetingid").focus();
      document.getElementById("meetingid").removeAttribute("readonly");
      document.getElementById("meetingcontent").removeAttribute("readonly");
    }
    else{
      this.showAlert("保存成功");
      this.wFlag = false;
      this.writeCon = "编辑";
      document.getElementById("meetingid").setAttribute('readonly','readonly');
      document.getElementById("meetingcontent").setAttribute("readonly",'readonly');
    }
  }
}



