import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { ApprovedpendingPage } from '../approvedpending/approvedpending';
import {HttpService} from "../../providers/HttpService";
/**
 * Generated class for the ApproveprojectdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approveprojectdetail',
  templateUrl: 'approveprojectdetail.html',
})
export class ApproveprojectdetailPage {

  //申明一个对象来装详情信息
  projectDetail:any={};
  //申明一个流程ID，根据流程ID获取详情数据
  processinstId:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController,private httpService:HttpService) {
    this.processinstId = navParams.get("processinstId");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApproveprojectdetailPage');
    this.getDetailPage();
  }

  //查询详情页面接口
  getDetailPage(){
    const url = "approveindialist/approveprojectdetail.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "processinstId":this.processinstId
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.projectDetail = res.resultRes;
      console.log("这是项目详情数据"+this.projectDetail);
    })

  }

  //点击提交
  submit(){
    let confirm = this.alertCtrl.create({
      title: '提示',
      message: '是否取消该走访计划?',
      buttons: [
        {
          text: '取消',
          handler: () => {
            console.log('您点击了取消');
          }
        },
        {
          text: '是',
          handler: () => {
            //您点击了提交
            this.navCtrl.push(ApprovedpendingPage,{approve_index:"2"},{ animate: true });
            console.log('您点击了是');
          }
        }
      ]
    });
    confirm.present();
  }

}



