import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BusinessreportdetailPage } from '../businessreportdetail/businessreportdetail';



/**
 * Generated class for the BusinessreportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-businessreport',
  templateUrl: 'businessreport.html',
})
export class BusinessreportPage {

  //申明一个集合来装从主页传递过来的值
  businessreportList:any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    let reprotArr  = navParams.get("reprotArrList");
    this.businessreportList = reprotArr;
    // console.log("传递过来的集合"+this.businessreportList.reverse());
    console.log(reprotArr.sort(this.compare('week')));
  }

  //根据字段week字段从大到小排列
  compare(property){
    return function(a,b){
    var value1 = a[property];
    var value2 = b[property];
    return value2 - value1;
  }
}
  // console.log(arr.sort(compare('age')))

  ionViewDidLoad() {
    console.log('ionViewDidLoad BusinessreportPage');
  }

  //点击其中一行，会转到对应详情页面
  go_detail(item){
    this.navCtrl.push(BusinessreportdetailPage,{reprotArrListLastWeek:item.week,startTime:item.startTime,endTime:item.endTime},
      { animate: true });
  }
}



