import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the CustomermanagerprodetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customermanagerprodetail',
  templateUrl: 'customermanagerprodetail.html',
})
export class CustomermanagerprodetailPage {

  //申明客户ID
  customId:any;
  //申明数据集合
  customerList:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService) {
      this.customId = navParams.get("customId");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CustomermanagerprodetailPage');
    this.getDetail();
  }

  //查询主界面的接口
  getDetail(){
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "customId":this.customId,
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.customerList =res;
      console.log(this.customerList);
    })

  }

}



