import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CustomermanagerprodetailPage } from '../customermanagerprodetail/customermanagerprodetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the CustomermanagerdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-customermanagerdetail',
  templateUrl: 'customermanagerdetail.html',
})
export class CustomermanagerdetailPage {

   //申明一个客户ID
  customId:any;
  //申明详情结合
  customDetail:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService) {
    this.customId = navParams.get("customId");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CustomermanagerdetailPage');
    this.getDetail();
  }

  //查看客户直辖项目列表
  go_customerdetail(){
    this.navCtrl.push(CustomermanagerprodetailPage,{customId:this.customId},{ animate: true });
  }

  //查询主界面的接口
  getDetail(){
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "customId":this.customId,
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.customDetail =res;
      console.log(this.customDetail);
    })

  }

}



