import { Component,OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
//import { CustomerDetailPage } from '../customerdetail/customerdetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-projectDetail',
  templateUrl: 'projectDetail.html',
})
export class ProjectDetailPage {
  projects:any={};
  instId:string;
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {
  	this.instId=navParams.get('instId');
  }

    ngOnInit() {
		const url = "search/projectDetail.json";
	   	const param={
	   		"instId":this.instId
	   	}
	    this.httpService.getData({
	      url: url,
	      body: param
	    })
	    .then(res=>{
	      console.log(res);
	      this.projects=res.projects;
	    })
	}
    share(){
    	console.log("分享");
    }

}



