import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,ViewController} from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";
import { AddattendPage } from '../addattend/addattend';

/**
 * Generated class for the AttendeesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-attendees',
  templateUrl: 'attendees.html',
})
export class AttendeesPage {
  attendLists:any=[];
  attendArr:any=[];
  attends:any={};
  
  checked_select:boolean=false;
  checked_all_select:boolean;

  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
    private httpService: HttpService,
    public viewCtrl: ViewController,
  ) {
  	this.getData();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AttendeesPage');
  }
	getData(){
		const url = "search/attenddList.json";
	   	const par={
	   	  	"type":this.attends.activityType,
	   	  	"name":this.attends.customer,
	   	  	"department":this.attends.department,
	   	  	"position":this.attends.position
	   	}
		this.httpService.getData({
		    url: url,
		    body: par
		})
	    .then(res=>{
			console.log(res);
			this.attendLists=res.meeters;
	    })
	}
  //点击全选
  all_check(){
    console.log("您点击了全选");
    //判断是否为true
    if(this.checked_all_select){
      this.checked_all_select = false;
      this.checked_select = false;
    }else{
      this.checked_all_select = true;
      this.checked_select = true;
      this.attendArr=this.attendLists;
    }
  }
	goSearch(){
		this.getData();
	}
  //选择类型
  switchType(){
    console.log("点击类型");
  }

  //选择职位
  switchType1(){
    console.log("点击部门");
  }

  //选择
  switchType2(){
    console.log("点击职位");
  }
  addList(){
  	console.log('新增');
  	 this.navCtrl.push(AddattendPage,{ animate: true });
  }
  addAtteends(){
  	console.log('立即添加');
  	console.log(this.attendArr);
  	this.viewCtrl.dismiss(this.attendArr);
  }
  selectName(item){
  	this.attendArr.push(item);
  }
}



