import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { ExaminerecordPage } from "../examinerecord/examinerecord";

/**
 * Generated class for the EnjoycardPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-enjoycard',
  templateUrl: 'enjoycard.html',
})
export class EnjoycardPage {
  headerText:any
  birthday:any = ""
  phoneNum:any = ""
  cardNum:any = ""
  companyName:any = ""
  contactlocation:any = ""

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public alertCtrl: AlertController) {
    this.headerText = this.navParams.get("title")
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad EnjoycardPage');
  }
  hintHandle (title) {
    if(title){
      if (title==this.headerText){
        title=title.substr(0,3)
      }
      let hint=this.alertCtrl.create({
        message: title+"不能为空",
        buttons: [
          {
            text: '确定',
            handler: data => {
              console.log('确定');
            }
          }
        ]
      })
      hint.present()
    }if(title=="") {
      let uptrue=this.alertCtrl.create({
        message: "提交成功",
        buttons: [
          {
            text: '确定',
            handler: data => {
              this.navCtrl.pop()
            }
          }
        ]
      })
      uptrue.present()
    }

  }
  Updata () {
    console.log(this.contactlocation+"---"+this.cardNum+"---"+this.phoneNum+"---"+this.birthday+"---"+this.companyName)
    let phoneNum=/^[1][3,4,5,7,8][0-9]{9}$/;
     if(this.cardNum.trim()==""){
      this.hintHandle(this.headerText)
       return false
     }
    if(!phoneNum.test(this.phoneNum.trim())){
        let uptrue=this.alertCtrl.create({
          message: "请填写正确的手机号码",
          buttons: [
            {
              text: '确定',
              handler: data => {
                console.log('确定');
              }
            }
          ]
        })
        uptrue.present()

      return false
    }
    if(this.birthday.trim()==""){
      this.hintHandle("生日")
      return false
    }
    if(this.companyName.trim()==""){
      this.hintHandle("单位名")
      return false
    }
    if(this.contactlocation.trim()==""){
      this.hintHandle("联系地址")
      return false
    }else {
      this.hintHandle("")
    }

  }

  goExamineRecord () {
    this.navCtrl.push(ExaminerecordPage)
  }
}



