import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,AlertController } from 'ionic-angular';
import { VisitlistPage } from '../visitlist/visitlist';

/**
 * Generated class for the VisidetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-visidetail',
  templateUrl: 'visidetail.html',
})
export class VisidetailPage {

  status_index:any;
  //已审批状态
  haveapprove:boolean=false;
  //退回原因
  canselreason:boolean=false;


  constructor(public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController) {
    this.status_index = navParams.get("index");
    console.log("您点击的是第"+this.status_index+"项");
    if(this.status_index == 1){
       this.haveapprove = false;
    }else if(this.status_index == 2){
      this.haveapprove = false;
    }else if(this.status_index == 3){
      this.haveapprove = true;
    }else if(this.status_index == 4){
      this.canselreason = true;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VisidetailPage');
  }

  cansel(){
    console.log("你点击了取消");
    let confirm = this.alertCtrl.create({
      title: '提示',
      message: '是否取消该走访计划?',
      buttons: [
        {
          text: '取消',
          handler: () => {
            console.log('您点击了取消');
          }
        },
        {
          text: '是',
          handler: () => {
            console.log('您点击了是');
            this.navCtrl.push(VisitlistPage,
              { animate: true });
          }
        }
      ]
    });
    confirm.present();
  }


}



