import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the ApprovedpenddetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approvedpenddetail',
  templateUrl: 'approvedpenddetail.html',
})
export class ApprovedpenddetailPage {

  //申明报告ID,是上个页面传过来的值，用来接收和显示
  reportId:any;
  //申明员工ID，是APP本身获取
  userId:any;
  //申明详情集合
  dependdingDetail:any;
  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApprovedpenddetailPage');
    this.getDetail();
  }

  //查询报告详情
  getDetail(){
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "reportId":this.reportId,
        "userId":this.userId,
        "approveMsg":"",
        "status":"",
        "approveDate":"",
        "processinstId":"",
        "workItemId":""
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.dependdingDetail =res;
      console.log(this.dependdingDetail);
    })

  }

}



