import { Component } from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {WorkbenchService} from "./workbenchService";
import { FormalcustPage } from "../formalcust/formalcust";
import { PotentialcustPage } from "../potentialcust/potentialcust";
import {MyapprovePage} from "../myapprove/myapprove";
import {BulletinPage} from "../bulletin/bulletin";
import { SearchlistPage } from '../searchlist/searchlist';

@IonicPage(
  {
    name: 'WorkBench'
  })
@Component({
  selector: 'page-workbench',
  templateUrl: 'workbench.html',
  providers:[WorkbenchService]
})

export class WorkBench {

  menus: Array<any> = [];
  Pageid:any
  constructor(public navCtrl: NavController,
              public service: WorkbenchService
  ) {

  }

  goTakepic (PageId) {
    if(PageId == "FormalcustPage") {
      this.Pageid = FormalcustPage
    }
    if(PageId == "PotentialcustPage") {
      this.Pageid = PotentialcustPage
    }
    if(PageId == "TargetplanPage") {
    }
    if (PageId == "TakepicPage") {
    }
    if (PageId == "Other") {
    }
    if (PageId == "MarketreportPage") {
    }
    if(PageId == "MyapprovePage"){
      this.Pageid = MyapprovePage
    }
    if(PageId == "BulletinPage"){
      this.Pageid = BulletinPage
    }
    if(PageId == "StatisticsPage"){
    }
    if(PageId == "MeetinginfoPage"){
    }
    this.navCtrl.push(this.Pageid)
  }

  ionViewDidLoad(){
    const param = {
      "num":1,
      "parent_id": "0"
    };
    this.service.getMenu(param).then(res=>{
      if(res.status){
        this.menus = res.data;
        console.log(res.data)
      }
    });
  }

  serachTool(){
  	console.log("kk");
    this.navCtrl.push(SearchlistPage,{},{ animate: true });
  }

}



