import {AfterViewInit, Component, EventEmitter, Input, OnChanges, Output, SimpleChanges} from '@angular/core';
import {ThirdPath} from "../../providers/third-path/third-path";
import {CalendarUtil} from "./calendar.util";

/**
 *  日历控件
 */
@Component({
  selector: 'calendar',
  templateUrl: 'calendar.html'
})
export class Calendar implements AfterViewInit, OnChanges{


  /**
   *  日历标题 头是否需要显示
   * @type {boolean}
   */
  @Input()
  calendarHeader: boolean = true;

  /**
   *  日历开关 左右控制
   * @type {boolean}
   */
  @Input()
  calendarOperate: boolean = true;

  /**
   * 日历模式 months:按月排日期  weeks:按周排日期
   * @type {string}
   */
  @Input()
  calendarModal: string = 'weeks';

  /**
   * 是否显示农历
   * @type {boolean}
   */
  @Input()
  showCNDate: boolean = false;

  /**
   * 不是本月的 无点击事件
   * @type {boolean}
   */
  @Input()
  canClickOutOfMonth: boolean = true;

  @Output()
  onDayEvent:EventEmitter<any> = new EventEmitter<any>();

  @Output()
  onChangeEvent:EventEmitter<any> = new EventEmitter<any>();

  @Output()
  onTitleChangeEvent:EventEmitter<any> = new EventEmitter<any>();

  @Output()
  onIsTodayEvent:EventEmitter<any> = new EventEmitter<any>();

  @Output()
  onModalChangeEvent:EventEmitter<any> = new EventEmitter<any>();//周、月的切换

  /**
   * @type {any[]}
   */
  weekdaysMin: string[] = ['日', '一', '二', '三', '四', '五', '六']

  /**
   * 日历标题
   */
  calendarTitle: string = ''

  /**
   * 日期列表
   * @type {any[]}
   */
  days:any = []

  /**
   * 选中的日期
   * @type {string}
   */
  selectedDate:string = ''

  /**
   * 当前显示的box
   * @type {string}
   */
  selectBox:string = ''

  @Input()
  daysEvents:any = {};

  constructor(
    public third: ThirdPath,
    public calendar:CalendarUtil) {

  }

  ngAfterViewInit(): void {
    this.setLocale();
    this.goToday();
  }

  /**
   * 设置为当天
   */
  public goToday = () => {
    this.setCalendarHeader(this.third.moment().format('LL'));
    let today = this.third.moment().format('LL');
    let day =  {date:today,fullDate:this.solar2lunar(today)};
    this.onDayClick(today,day);
  }

  /**
   * 设置地区
   */
  private setLocale = () => {
    this.third.moment.locale('zh-cn', {
      longDateFormat: {
        L: "YYYY/MM",
        LL: "YYYY/MM/DD",
        l: "YYYY年M月",
        ll: "YYYY年MM月DD日"
      }
    });
  }

  /**
   * 设置日历标题
   * @param {string} date
   */
   setCalendarHeader = (date: string) => {
    switch (this.calendarModal) {
      // case 'weeks':
      //   this.calendarTitle = `${this.third.moment(date, 'LL').format('YYYY年')}
      //   第${this.third.moment(date, 'LL').week()}周`
      //   break;

      default:
        this.calendarTitle = `${this.third.moment(date, 'LL').format('l')}`
        break;
    }
    this.onTitleChangeEvent.emit(this.calendarTitle);
    this.setDays(date)
  }

  /**
   * 设置日历日期
   * @param {string} date
   */
  private setDays = (date: string) => {
    switch (this.calendarModal) {
      // case 'weeks':
      //   this.setDaysByWeeks(date)
      //   break;
      case 'months':
        this.setDaysByMonths(date)
        break;

      default:
        this.calendarModal = 'weeks'
        this.setDaysByWeeks(date)
        break;
    }
    this.onChangeEvent.emit(date)
  }

  /**
   * 通过月设置日历日期
   * @param {string} date
   */
  private setDaysByMonths = (date: string) => {
    this.days = []
    this.selectBox = this.third.moment(date, 'LL').startOf('month').format('LL')
    //1.计算当前日期这个月最后一天的日期
    let lastDateOfMonth = this.third.moment(date, 'LL').endOf('month')
    let lastDayOfMonth = lastDateOfMonth.get('date');
    //2.计算这个月1号是星期几
    let startDateOfMonth = this.third.moment(date, 'LL').startOf('month')
    let firstWeek = startDateOfMonth.isoWeekday() == 7 ? 0 : startDateOfMonth.isoWeekday();
    let endWeek = lastDateOfMonth.isoWeekday() == 7 ? 0 : lastDateOfMonth.isoWeekday();

    let day:any;
    //3.计算上个月留几天在本月页面
    for (let i = firstWeek; i > 0; i--) {
      let obj = this.third.moment(date, 'LL').startOf('month').subtract(i, 'days').format('LL')
      day =  {date:obj,fullDate:this.solar2lunar(obj)}
      this.days.push(day)
    }

    //4.当前月份的天数列表
    for (let i = 0; i < lastDayOfMonth; i++) {
      let obj = this.third.moment(date, 'LL').startOf('month').add(i,'days').format('LL')
      day =  {date:obj,fullDate:this.solar2lunar(obj)}
      this.days.push(day)
    }

    //5.计算下个月留几天在本月页面
    for (let i = 1; i <= 6 - endWeek; i++) {
      let obj = this.third.moment(date, 'LL').endOf('month').add(i, 'days').format('LL')
      day =  {date:obj,fullDate:this.solar2lunar(obj)}
      this.days.push(day)
    }

  }

  /**
   * 通过周设置日历日期
   * @param {string} date
   */
  private setDaysByWeeks = (date: string) => {
    this.days = []
    this.selectBox = this.third.moment(date, 'LL').startOf('weeks').format('LL')
    let day:any
    for (let i = 0; i < 7; i++) {
      let obj = this.third.moment(this.selectBox, 'LL').add(i, 'days').format('LL')
      day =  {date:obj,fullDate:this.solar2lunar(obj)}
      this.days.push(day)
    }
  }


  /**
   * 下个月或者下周
   */
  next = () =>{

    switch (this.calendarModal){
      case 'weeks':
        this.selectBox = this.third.moment(this.selectBox,'LL').add(7,'days').format('LL')
        this.setCalendarHeader(this.selectBox);
        let day =  {date:this.selectBox,fullDate:this.solar2lunar(this.selectBox)};
        this.onDayClick(this.selectBox,day);
        break;

      default:
        this.selectBox = this.third.moment(this.selectBox,'LL').add(1,'months').format('LL')
        this.setCalendarHeader(this.selectBox);
        let daySelect =  {date:this.selectBox,fullDate:this.solar2lunar(this.selectBox)};
        this.onDayClick(this.selectBox,daySelect);
        break;
    }

  }

  /**
   * 上个月或上周
   */
  previous = () =>{
    switch (this.calendarModal){
      case 'weeks':
        this.selectBox = this.third.moment(this.selectBox,'LL').subtract(7,'days').format('LL')
        this.setCalendarHeader(this.selectBox);
        let daySelect =  {date:this.selectBox,fullDate:this.solar2lunar(this.selectBox)};
        this.onDayClick(this.selectBox,daySelect);
        break;

      default:
        this.selectBox = this.third.moment(this.selectBox,'LL').subtract(1,'months').format('LL')
        this.setCalendarHeader(this.selectBox);
        let day =  {date:this.selectBox,fullDate:this.solar2lunar(this.selectBox)};
        this.onDayClick(this.selectBox,day);
        break;
    }
  }

  /**
   * 模式切换为 weeks
   */
  onPanUp = () =>{
    if(this.calendarModal != 'weeks'){
      this.calendarModal = 'weeks'
      let today = this.third.moment().format('LL')
      if(this.third.moment(this.selectBox,'LL').format('L') == this.third.moment(today,'LL').format('L')){
        if(this.selectedDate){
          if(this.third.moment(this.selectBox,'LL').format('L') == this.third.moment(this.selectedDate,'LL').format('L')){
            this.setCalendarHeader(this.selectedDate)
          }
          else{
            this.setCalendarHeader(this.selectBox)
          }
        }
        else{
          this.setCalendarHeader(today)
        }
      }
      else{
        if(this.selectedDate){
          if(this.third.moment(this.selectBox,'LL').format('L') == this.third.moment(this.selectedDate,'LL').format('L')){
            this.setCalendarHeader(this.selectedDate)
          }
          else{
            this.setCalendarHeader(this.selectBox)
          }
        }
        else{
          this.setCalendarHeader(this.selectBox)
        }
      }
      let day =  {date:this.selectedDate,fullDate:this.solar2lunar(this.selectedDate)};
      this.onDayClick(this.selectedDate,day);

    }
  }

  /**
   * 模式切换为 moths
   */
  onPanDown = () =>{
    if(this.calendarModal != 'months'){
      this.calendarModal = 'months'
      let today = this.third.moment().format('LL')
      console.log(this.selectBox)
      let start = this.third.moment(this.selectBox,'LL').startOf('weeks').format('LL');
      let end = this.third.moment(this.selectBox,'LL').endOf('weeks').format('LL');
      if(today<start || today>end){
        if(this.selectedDate){
          if(this.selectedDate<start || this.selectedDate>end){
            this.setCalendarHeader(this.selectBox)
          }
          else{
            this.setCalendarHeader(this.selectedDate)
          }
        }
        else{
          this.setCalendarHeader(this.selectBox)
        }
      }
      else {
        if(this.selectedDate){
          if(this.selectedDate<start || this.selectedDate>end){
            this.setCalendarHeader(today)
          }
          else{
            this.setCalendarHeader(this.selectedDate)
          }
        }
        else{
          this.setCalendarHeader(this.selectBox)
        }
      }
      let day =  {date:this.selectedDate,fullDate:this.solar2lunar(this.selectedDate)};
      this.onDayClick(this.selectedDate,day);
      // this.onModalChangeEvent.emit(this.selectedDate);
    }
  }



  onDayClick = (date:string,day) =>{
    switch (this.calendarModal){
      case 'weeks':
        // if(this.selectedDate == date) {
        //   // this.selectedDate = '';
        //   return;
        // }
        this.selectedDate = date;
        // this.onDayEvent.emit(day);
        day.hasEvent = this.hasEvent(date);
        this.onDayEvent.emit(day);
        this.onIsTodayEvent.emit(this.isToday(date));
        break;

      default:
        if(this.third.moment(date,'LL').format('L')<this.third.moment(this.selectBox,'LL').format('L')){
          if(this.canClickOutOfMonth){
            this.previous();
          }
        }
        else if(this.third.moment(date,'LL').format('L')>this.third.moment(this.selectBox,'LL').format('L')){
          if(this.canClickOutOfMonth){
            this.next();
          }
        }
        else{
          // if(this.selectedDate == date) {
          //   // this.selectedDate = '';
          //   return;
          // }
          this.selectedDate = date;
          // this.onDayEvent.emit(date);
          day.hasEvent = this.hasEvent(date);
          this.onDayEvent.emit(day);
          this.onIsTodayEvent.emit(this.isToday(date));
        }
        break;
    }

  }


  /**
   * 设置Wrapper的class
   */
  setWrapper = ():string =>{
    if(this.calendarModal == 'weeks') return 'calendar-model-w'
    return 'calendar-model-m'
  }


  /**
   * 不是当前月
   * @param {string} date
   * @returns {boolean}
   */
  outOfMonth = (date:string) =>{
    if(!this.selectBox) return false;

    if(this.calendarModal == 'weeks') return false;

    date = this.third.moment(date,'LL').format('L')

    if(this.third.moment(this.selectBox,'LL').format('L') != date) return true

    return false;

  }

  /**
   * 是否是今天
   * @param date
   * @returns {boolean}
   */
  isToday = (date) =>{
    let today = this.third.moment().format('LL');
    if(date == today) return true;

    return false
  }

  /**
   * 休息日判断
   * @param date
   * @returns {boolean}
   */
  dayOff = (date) =>{
    let week = this.third.moment(date,'LL').isoWeekday();
    if(week == 6 || week == 7) return true;
    return false
  }

  /**
   * 是否今天有事件
   * @param date
   * @returns {boolean}
   */
  hasEvent = (date) => {
    if(!this.daysEvents || !this.daysEvents[date]) return false
    return true;
  }


  ngOnChanges(changes: SimpleChanges): void {
    if(changes.daysEvents){
      this.daysEvents = changes.daysEvents.currentValue
    }
  }


  private solar2lunar = (date:string):any =>{
    let year = this.third.moment(date, 'LL').get('year')
    let month = this.third.moment(date, 'LL').get('month')
    let day = this.third.moment(date, 'LL').get('date')
    let fullDate = this.calendar.solar2lunar(year,month+1,day)
    return fullDate
  }


}



