import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { AccountDetailPage } from '../accountDetail/accountDetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-accountSearchpage',
  templateUrl: 'accountSearch.html',
})
export class AccountSearchPage {
  accounts:any={};
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {}

   goRateDetail(search){
   	console.log('去客户详情');
   	const url = "search/accountSearch.json";
	   	const param={
	   		"name":this.accounts.name,
	   		"AccountId":this.accounts.AccountId,
	      	"beginDate":this.accounts.start,
	      	"endDate":this.accounts.end,
	      	"pageNo":"1",
	      	"dataCount":"10",
	      	"firstPageNo":"10"
	   	}
	    this.httpService.getData({
	      url: url,
	      body: param
	    })
	    .then(res=>{
	      console.log(res);
		  this.navCtrl.push(AccountDetailPage,{"accountDetails":res},{ animate: true });
	    })
   	
   }
   presentModal(){
   	console.log("分享");
   }

}



