import {Injectable} from '@angular/core';
import 'rxjs/add/operator/toPromise';
import {Contents} from "./Constants";
import {HttpRequest} from "./HttpRequest";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Http} from "@angular/http";

@Injectable()
export class HttpService {

  constructor(private http: HttpClient) {
  }
  private server:string = Contents.HTTP_DATA_LOCAL;
  // private server:string = Contents.HTTP_DATA_DEBUG;
  // private server:string = Contents.HTTP_DATA_PROD;

  static PUT = 'put';
  static POST = 'post';
  static DELETE = 'delete';

  public requestGet(request: HttpRequest) {
    return this.http.get(request.url + '?'+ request.body,request.options)
      .toPromise()
      .then(res => this.handleSuccess(res))
      .catch(error => this.handleError(error));
  }
  public requestPost(request: HttpRequest) {
    return  this.http.post(request.url,request.body,request.options)
      .toPromise()
      .then(res => this.handleSuccess(res))
      .catch(error => this.handleError(error));
  }

  //put 增加
  public requestPut(request: HttpRequest) {
    return this.http.put(request.url,request.body,request.options)
      .toPromise()
      .then(res => this.handleSuccess(res))
      .catch(error => this.handleError(error));
  }

  //delete 删除
  public requestDelete(request: HttpRequest) {
    return  this.http.delete(request.url,request.options)
      .toPromise()
      .then(res => this.handleSuccess(res))
      .catch(error => this.handleError(error));
  }
  requestMethod:any;
  postData = (request?:HttpRequest) =>{
    this.requestMethod = HttpService.POST;
    return this.getData(request);
  };
  deleteData = (request?:HttpRequest) =>{
    this.requestMethod =  HttpService.DELETE;
    return this.getData(request);
  };
  putData = (request?:HttpRequest) =>{
    this.requestMethod = HttpService.PUT;
    return this.getData(request);
  };

  getData = (request?:HttpRequest) =>{

    const url = this.getAction(request.url);
    request.url = url;
    const headers = new HttpHeaders({
      'Content-Type':'application/json;charset=utf-8'
      // 'X-Custom-Header':''
    });

    request.options = {headers:headers};

    request.body =request.body; // this.toJsonBody(request.body);

    request = request.requestChange&&request.requestChange(request)||request;

    switch (this.server){
      case Contents.HTTP_DATA_DEBUG:
        return this.requestGet(request);
      default:
        switch (this.requestMethod){
          case HttpService.POST:
            return  this.requestPost(request);
          case HttpService.PUT:
            return  this.requestPut(request);
          case HttpService.DELETE:
            return  this.requestDelete(request);
          default:
            return this.requestGet(request);
        }
    }

  };
  private handleSuccess(result) {
    if (result && !result.success) {//由于和后台约定好,所有请求均返回一个包含success,msg,data三个属性的对象,所以这里可以这样处理
      // alert("result.msg");//这里使用ToastController
    }
    return result;
  }
  private handleError(error: Response | any) {
    let msg = '请求失败';
    if (error.status == 0) {
      msg = '请求地址错误';
    }
    if (error.status == 400) {
      msg = '请求无效';
      console.log('请检查参数类型是否匹配');
    }
    if (error.status == 404) {
      msg = '请求资源不存在';
      console.error(msg+'，请检查路径是否正确');
    }
    console.log(error);
    // alert("msg");//这里使用ToastController
    return {success: false, msg: msg};
  }
  private getLocationOrigin = (port?: string): string => {
    return window.location.protocol + '//' + window.location.hostname + (port ? ':' + port : '');
  }

  /**
   *
   * @param any
   * @return {string}
   *  声明: var obj= {'name':'小军',age:23};
   *  调用: toQueryBody(obj);
   *  返回: "name=%E5%B0%8F%E5%86%9B&age=23"
   */
  private toQueryBody = (params: any):string => {

    let ret = [];
    for (let key in params) {
      key = encodeURIComponent(key);
      let values = params[key];
      if (values && values.constructor == Array) {
        let queryValues = [];
        for (let i = 0, len = values.length, value; i < len; i++) {
          value = values[i];
          queryValues.push(this.toQueryPair(key, value));
        }
        ret = ret.concat(queryValues);
      }
      else {
        ret.push(this.toQueryPair(key, values))
      }
    }

    return ret.join('&')
  }
  private toQueryPair(key, value) {
    if (typeof value == 'undefined') {
      return key;
    }
    return key + '=' + encodeURIComponent(value === null ? '' : String(value));
  }

  public getAction = (action:string):string =>{
    let url = '';
    if(action.indexOf("json")!=-1){
      this.server=Contents.HTTP_DATA_DEBUG;
    }else{
      this.server=Contents.HTTP_DATA_LOCAL;
    }
    switch (this.server){
      case Contents.HTTP_DATA_LOCAL:
        url = action;
        // url=Contents.HTTP_LOCAL_ORIGIN;
        // url = this.getLocationOrigin(Contents.HTTP_LOCAL_PORT) + Contents.HTTP_PROJECT + '/' + action;
        break;

      case Contents.HTTP_DATA_DEBUG:
        url = Contents.HTTP_DATA_PATH + '/' + action;
        break;

      default:
        url = Contents.HTTP_PROJECT + '/' + action;
        break;
    }

    return url;
  }
  private toJsonBody = (obj: any):string => {
    let str = JSON.stringify(obj);
    return str;
  }
}



