import {ChangeDetectorRef, Component, ViewChild} from '@angular/core';
import {Content, IonicPage, NavController} from 'ionic-angular';
import {HomeService} from "./HomeService";
import {HttpService} from "../../providers/HttpService";
import { ApprovedpendingPage } from '../approvedpending/approvedpending';
import { BusinessreportPage } from '../businessreport/businessreport';
import { ApprovedpenddetailPage } from '../approvedpenddetail/approvedpenddetail';
import { BusinessreportdetailPage } from '../businessreportdetail/businessreportdetail';
import { VisitlistPage } from '../visitlist/visitlist';
import { VisidetailPage } from '../visidetail/visidetail';
import { addreportPage } from '../addreport/addreport';

@IonicPage(
  {
    name: 'HomePage',
  })
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  providers:[HomeService]
})

export class HomePage {

  //主界面的数据包装
  homePageDrtail:any=[];
  //申明每周业务报告的列表集合(点击更多的时候，将该集合传入每周报告列表页面)
  reprotArrList:any;
  //申明每周业务报告首页展示的最后一条数据
  reprotArrListLastItem:any;
  reprotArrListLastWeek:any;
  reprotArrListLastTime:any;
  //empID
  empId:any = 1;

  DTime1:any;
  DTime2:any;

  //申明页面展示字段
  reportNum:any;
  proNum:any;

  //传递待审批更多的字段
  constructor(public navCtrl: NavController,
                private httpService:HttpService
              ) {

  }

  //点击进入走访详情页面
  go_visidetail(status){
    // let index;
    // if(status == '待走访'){
    //   index = 1;
    // }else if(status == '已走访'){
    //   index = 2;
    // }else if(status == '已提交待审批'){
    //   index = 3;
    // }else if(status == '已取消'){
    //   index = 4;
    // }else if(status == '过期未走访'){
    //   index = 5;
    // }
    // this.navCtrl.push(VisidetailPage,{index:index},
    //   { animate: true });
    this.navCtrl.push(addreportPage,
      { animate: true });
  }

  //点击首页的待审批项，进入该项的详情
  approvedpendingdetail(){
    this.navCtrl.push(ApprovedpenddetailPage,
      { animate: true });
  }

  //点击首页的未完成走访计划
  go_visitlist(){
    this.navCtrl.push(VisitlistPage,
      { animate: true });
  }

  //点击首页的业务报告，进入该项的详情
  go_businessreportdetail(){
    this.navCtrl.push(BusinessreportdetailPage,{reprotArrListLastWeek:this.reprotArrListLastWeek,startTime:this.reprotArrListLastItem.startTime,endTime:this.reprotArrListLastItem.endTime},
      { animate: true });
  }

  //转到审批列表
  go_approverpend(index){
    console.log("你点击了待审批列表");
    //点击更多，查询类型的最大数据
    console.log(this.homePageDrtail);
    if(index == 0){
      var arrNum = [
        {name:"用印",num:this.homePageDrtail.recordNum},
        {name:"项目",num:this.homePageDrtail.reportNum},
        {name:"尽调",num:this.homePageDrtail.proNum},
        {name:"报告",num:this.homePageDrtail.num}
      ]
      console.log("根据排序来的："+arrNum.sort(this.compare('num')));
      console.log("最多条数的是："+arrNum[0].name);
      let indexName = arrNum[0].name;
      if(indexName == '用印'){
        index = 1;
      }else if(indexName == '项目'){
        index = 2;
      }else if(indexName == '尽调'){
        index = 3;
      }else if(indexName == '报告'){
        index = 4;
      }
      this.navCtrl.push(ApprovedpendingPage,{"approve_index":index},
        { animate: true });
    }else{
      this.navCtrl.push(ApprovedpendingPage,{"approve_index":index},
        { animate: true });
    }
  }

  //转到每周维护页面
  go_report(){
    this.navCtrl.push(BusinessreportPage,{reprotArrList:this.reprotArrList},
      { animate: true });
  }

  //排序共用方法(从大到小排列)
  compare(property){
    return function(a,b){
      var value1 = a[property];
      var value2 = b[property];
      return value2 - value1;
    }
  }

  @ViewChild(Content) content: Content;

  ionViewDidLoad() {

    this.getHomePage();

    var arrNewList = [];

    //获取当前周
    this.DTime1 = new Date();
    this.DTime2 = new Date();
    this.DTime2.setMonth(0);
    this.DTime2.setDate(1);
    let rq = this.DTime1 - this.DTime2;
    var s1 = Math.ceil(rq/(24*60*60*1000));
    var weekofyear = Math.ceil(s1/7);
    console.log("这是当前周"+weekofyear);
    //查询主界面的接口
    var getWeeks = function(year){
      var d = new Date(year, 0, 1);
      while (d.getDay() != 1) {
        d.setDate(d.getDate() - 1);
      }
      var to = new Date(year + 1, 0, 1);
      var i = 1;

      var arrList = [];
      for (var from = d; from < to;) {
        var str = '';
        str = i + "&" +year+"-"+ ((from.getMonth() + 1)<=9?('0'+(from.getMonth() + 1)):(from.getMonth() + 1)) + "-" + (from.getDate()<=9?('0'+from.getDate()):from.getDate()) + "&";
        from.setDate(from.getDate() + 6);
        if (from < to) {
          str = str + year +"-"+ ((from.getMonth() + 1)<=9?('0'+(from.getMonth() + 1)):(from.getMonth() + 1)) + "-" + (from.getDate()<=9?('0'+from.getDate()) :from.getDate());
          from.setDate(from.getDate() + 1);
        } else {
          to.setDate(to.getDate() - 1);
          str = str + year +"-"+ ((to.getMonth() + 1)<=9?('0'+(to.getMonth() + 1)):(to.getMonth() + 1)) + "-" + (to.getDate()<=9?('0'+to.getDate()) : to.getDate());
        }
        arrList.push(str);
        i++;
      }

      //重新组装arrList,截取每个数组变成每个新的对象
      for(var o = 0;o < weekofyear;o++){
        //新建一个对象
        let weekReprotList = {};
        weekReprotList["week"] = arrList[o].split("&")[0];
        weekReprotList["startTime"] = arrList[o].split("&")[1];
        weekReprotList["endTime"] = arrList[o].split("&")[2];
        arrNewList.push(weekReprotList);
      }

      //判断如果是当周，且最后日期大于当天查看日期，就显示当天日期
      var endTimeCurrentWeek = arrNewList[arrNewList.length - 1].endTime;
      //获取当前日期
      var dateTime = new Date();
      var currYear  = dateTime.getFullYear();
      var currMonth = dateTime.getMonth() + 1;
      var currDate = dateTime.getDate();
      var currMonthString:any;
      var currDateString:any;
      if(currMonth >= 1 && currMonth <=9){
        currMonthString = "0"+currMonth;
      }else{
        currMonthString = currMonth;
      }
      if(currDate >=1 && currDate <=9){
        currDateString = "0"+ currDate;
      }else{
        currDateString = currDate;
      }
      //申明当天日期
      var currDateTime = currYear + "-" + currMonthString +"-" + currDateString;
      //转换时间格式进而进行比较
      var d1 = new Date(currDateTime.replace(/\-/g,"\/"));
      var d2 = new Date(endTimeCurrentWeek.replace(/\-/g,"\/"));
      if(d1<d2){
        arrNewList[arrNewList.length - 1].endTime = currDateTime;
      }
    }
    //传入当前年
    getWeeks(new Date().getFullYear());
    this.reprotArrList = arrNewList;
    this.reprotArrListLastItem = arrNewList[arrNewList.length - 1];
    this.reprotArrListLastWeek = this.reprotArrListLastItem.week;
    this.reprotArrListLastTime = this.reprotArrListLastItem.startTime+" 至 "+this.reprotArrListLastItem.endTime;
  }
  //时间字段格式化
  timeFormat(val) {
    return val.slice(0,10);
  }

  //查询主界面的接口
  getHomePage(){
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "userId":this.empId
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.homePageDrtail =res.resultRes.plans.slice(0,3);
      console.log(new Date(this.homePageDrtail[0].planDate).getDay());
      console.log(res.resultRes);
    })

  }

  //返回的时间对象中取周几
  getWeekday(time) {
    const arr = ["周日","周一","周二","周三","周四","周五","周六"];
    return arr[new Date(time).getDay()];
  }


}



