import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import {AddOtherPage} from "../addotherpage/addotherpage";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-otherdetailpage',
  templateUrl: 'otherdetailpage.html',
})
export class OtherDetailPage {
  otherDetails:any;
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
  public modalCtrl: ModalController) {
  	this.otherDetails=navParams.get('detail');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OtherDetailPage');
  }

   presentModal() {
    let myModal = this.modalCtrl.create(AddOtherPage,{ otherDetails: this.otherDetails,type:"编辑其它" });
     myModal.onDidDismiss(data => {
	 this.otherDetails=data;
   });
    myModal.present();
  }

}



