import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the ReportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */



@IonicPage()
@Component({
  selector: 'page-report',
  templateUrl: 'report.html',
})
export class ReportPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public httpService:HttpService,
              public alertCtrl: AlertController) {
  }
  title:any = "修改";
  changeBtn:any = "编辑";
  theme:any;
  content:any;
  read:boolean = true;
  test:boolean = this.navParams.get('animate');

  ionViewWillEnter() {
    console.log('ionViewDidLoad ReportPage');
    const url = "market/updateMarkReport.json";
    this.httpService.postData({
      url: url,
      body: {
      }
    }).then(res=>{
      // console.log(res);
      console.log(this.test);

      if(res.status == 0){
        this.theme = res.resultRes.theme;
        this.content = res.resultRes.content;
      }
      //status为1时表示当前为新增报告
      else if(res.status == 1)
      {
        this.title ="新增";
        this.changeBtn ="保存";
        this.read = false ;
      }
      else{
        this.showAlert();
      }

    })

  }

  saveReport(theme,conten){
    if(this.changeBtn == "编辑")
    {
      // angular.element
      this.changeBtn = "保存";
      this.read = false ;
    }
    else if(this.changeBtn == "保存") {
      this.read = true ;
      console.log(theme + conten);
      this.changeBtn = "编辑";
      // this.navCtrl.pop();
      const url = "performance/customerCurrent.json";
      this.httpService.getData({
        url: url,
        body: {
          theme: theme,
          conten: conten
        },
      })
        .then(res => {
          let result = res.status;
          //alert(result);
          if (result == 0) {
            this.showAlertMsg(this.title + "成功");
            this.ionViewWillEnter();
          }
          else {
            this.showAlertMsg(this.title + "失败");
            //alert("登陆失败");
            // let toast = this.toastCtrl.create({
            //   message: '登陆失败',
            //   duration: 3000,//3秒后自动消失
            //   position: 'top'//位置
            // })
            // toast.present();
          }
        }).catch(error => {

        this.showAlertMsg(this.title + "错误");
      })
    }
  }

  showAlert() {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: '暂无数据',
      buttons: ['确定']
    });
    alert.present();
  }
  showAlertMsg(messag) {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: messag,
      buttons: ['确定']
    });
    alert.present();
  }


}



