import { Component } from '@angular/core';
import {AlertController, NavController,NavParams,ModalController,ViewController} from 'ionic-angular';
import {enableProdMode} from '@angular/core';
import {HttpService} from "../../providers/HttpService";
import { Platform, ActionSheetController } from 'ionic-angular';
import {AttendeesPage} from "../attendees/attendees";

enableProdMode();
@Component({
  selector: 'page-addreportpage',
  templateUrl: 'addreport.html'
})
export class addreportPage {

  people:any=[];
  peerpames: any = [];
  wFlag:boolean;
  myList:any = [];
  toggle:any = [];
  stateSelectval:any = "周会";
  toggleSelect:any = "";
  newtime:any={};
  minTime:string;
  isSelected:boolean=false;
  isBlock:string;
  showPromp:boolean=false;
  isShowEdit:boolean=false;
  attends:any=[];
  isDisplayAttend:string='none';
	reports:any={};
	isSearch:string;
	searchLists:any=[];
  location:string="";
  xLocation:number;
  yLocation:number;
  errLocation:string;

  select ={
    ismyListshow:false,
    isUnderListshow:false,
    MyList:[
      {option:"周会" ,ischecked:false},
      {option:"晨会" ,ischecked:false},
      {option:"夕会" ,ischecked:false}
    ]
  }

  constructor(
    public platform: Platform,
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl:AlertController,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,
    private httpService: HttpService

  ) {
  	//获取用户userId
  	this.reports.userId=sessionStorage.getItem("userName");
  	this.isSearch='none';

  	//是否显示提示
		this.showPromp=navParams.get('showPromp');
		if(this.showPromp){
			this.isBlock="block";
		}else{
			this.isBlock="none";
		}

		//判断是否新增
		this.reports.isAdd=navParams.get('isAdd');
		//1是编辑
		if(this.reports.isAdd==1){
			this.reports=navParams.get('datas');
			this.reports.reportType=0;
			console.log(this.reports);

		}else{
			//新增走访进来
			this.reports.reportType=1;
			this.reports.status=1;
			this.reports.planId="";
		}

    //把原来的构造方法删了
    this.reports.starttime = new Date(new Date().getTime()+8*60*60*1000+30*60*1000).toISOString();
    this.reports.endtime = new Date(new Date().getTime()+8*60*60*1000+60*60*1000+30*60*1000).toISOString();
    let minDate=new Date();
    const year=minDate.getFullYear();
    const month=minDate.getMonth()+1;
    const day=minDate.getDate();
    this.minTime=year+"-"+(month<10?"0"+month:month)+"-"+(day<10?"0"+day:day);
    console.log(this.minTime.toString());
////  console.log(this.minTime.toISOString());
//		this.minTime="2018-03-29";


  }


  ionViewDidLoad() {
    this.wFlag=false;
  }

  //设置单选列表状态
  showMyList (toggle) {
    this.toggleSelect = toggle
    this.myList = this.select.MyList
    if(toggle == "stat") {
      this.select.isUnderListshow = false
      if (this.select.ismyListshow) {
        this.select.ismyListshow = false
      }else {
        this.select.ismyListshow = true
      }
    }
  }

  //设置筛选内容
  getValues (val) {
    if(this.toggleSelect === "stat"){
      this.stateSelectval = val
    }
    this.select.ismyListshow = false
  }

  /*clockIn(){
    //console.log(111);
    if(this.wFlag == false){
      document.getElementById('starttime').style.display="none";
      document.getElementById('endtime').style.display="none";
      this.wFlag=true;
    }else{
      document.getElementById('starttime').style.display="block";
      document.getElementById('endtime').style.display="block";
      this.wFlag=false;
    }
  }*/

  showAlert(msg) {
    let alert = this.alertCtrl.create({
      title: '系统提示',
      subTitle: msg,
//    buttons: ["确定"]
      buttons: [{
      		text:"确定",
      		handler: () => {
		      console.log('Cancel clicked');
						this.viewCtrl.dismiss();
		    }
      }]
    });
    alert.present();
  }
  /*tomeetpeople(){
    this.navCtrl.push(MeetingpeoplePage,{ animate: true });
    //console.log(111111);
  }*/

  saveRemark(){
    this.showAlert("保存成功");
    //console.log(starttime);
    //this.navCtrl.pop();
  }

  gotochoosepeo(){
  }

  //跳转回调
  CallbackFunction = (_params) => {
    return new Promise((resolve, reject) => {

      this.peerpames = _params;
      console.log(this.peerpames.length);
      if(this.peerpames.length>3){
        this.people = this.peerpames.slice(0,3)+",...";
        console.log(this.people)
      }else {
        this.people = this.peerpames;
      }

      resolve();
    });
  }

changeDate(e){
    let date = new Date(this.reports.starttime);
    console.log(date);
    this.reports.endtime=new Date(date.getTime()+60*60*1000).toISOString();
    console.log(this.reports.endtime);
    this.isShowEdit=true;
}
  addAttendees(evevt){
  	console.log(evevt);
  	console.log("添加与会列表");
  	let AattendeesModal = this.modalCtrl.create(AttendeesPage,{type:"编辑其它" });
	  AattendeesModal.onDidDismiss(data => {
	//	 this.otherDetails=data;
				console.log(data);
				this.attends=data;
				if(data){
					this.isDisplayAttend='block';
				}

	   });
	  AattendeesModal.present();
  }

  isSelect(){
  	console.log(this.isSelected);
  }
   saveOther(){
  	console.log('关闭提醒');
  	this.isBlock='none';
  }
   addReport(isSubmit){
   	console.log("提交");
   	 const par={
   	  	"planId":this.reports.planId,
   	  	"status":this.reports.status,
   	  	"customName":this.reports.customer,
   	  	"pace":this.reports.pace,
   	  	"planDate":this.reports.starttime,
   	  	"address":this.reports.address,
   	  	"realDate":this.reports.endtime,
   	  	"meeeters":this.attends,
   	  	"meetiType":this.stateSelectval,
   	  	"mainpPurpose":this.reports.mainpPurpose,
   	  	"customData":this.reports.customData,
   	  	"talkProcess":this.reports.talkProcess,
   	  	"talkResult":this.reports.talkResult,
   	  	"otherMsg":this.reports.otherMsg,
   	  	"photo":this.reports.photo,
   	  	"userId":this.reports.userId,
   	  	"isAdd":this.reports.isAdd,
   	  	"isSubmit":isSubmit,
   	  	"reportType":this.reports.reportType
   	  }
   	this.showSelect('确定要提交吗',par);
   }
   editReport(isSubmit){
   	console.log("改期");
   	 const par={
   	  	"planId":this.reports.planId,
   	  	"status":this.reports.status,
   	  	"customName":this.reports.customer,
   	  	"pace":this.reports.pace,
   	  	"planDate":this.reports.starttime,
   	  	"address":this.reports.address,
   	  	"realDate":this.reports.endtime,
   	  	"meeeters":this.attends,
   	  	"meetiType":this.stateSelectval,
   	  	"mainpPurpose":this.reports.mainpPurpose,
   	  	"customData":this.reports.customData,
   	  	"talkProcess":this.reports.talkProcess,
   	  	"talkResult":this.reports.talkResult,
   	  	"otherMsg":this.reports.otherMsg,
   	  	"photo":this.reports.photo,
   	  	"userId":this.reports.userId,
   	  	"isAdd":this.reports.isAdd,
   	  	"isSubmit":isSubmit,
   	  	"reportType":this.reports.reportType
   	  }
   	this.showSelect('是否确认改期',par);
   }
   cancelReport(){
   	console.log("取消");
// 	this.showSelect('会提示该操作会导致该走访计划变为已取消状态，是否确认取消');
   	this.presentPrompt('会提示该操作会导致该走访计划变为已取消状态，是否确认取消');
   }
   showSelect(msg,param) {
    let alert = this.alertCtrl.create({
      title: '提示',
      subTitle: msg,
//    buttons: ["确定"]
      buttons: [
      	{
        text: '取消',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      },
      {
      		text:"确定",
      		handler: () => {
		      console.log('提交');
		      this.raise(param);

		    }
      }]
    });
    alert.present();
  }
   presentPrompt(msg) {
	  let alert = this.alertCtrl.create({
	    title: '提示',
	    subTitle: msg,
	    inputs: [
	      {
	        name: 'cancelause',
	        placeholder: '请输入取消原因,否则无法取消'
	      }
	    ],
	    buttons: [
	      {
	        text: '取消',
	        role: 'cancel',
	        handler: data => {
	          console.log('Cancel clicked');
	        }
	      },
	      {
	        text: '确定',
	        handler: data => {
	        	if(data.cancelause){
	        		console.log("有数据发请求------");
	        		console.log(data.cancelause);
	        		//向后台请求取消

	        		this.viewCtrl.dismiss();
	        	}else{
	        		console.log("---+++++++++++++--");
	        		console.log(data.cancelause);
	        	}

	        }
	      }
	    ]
	  });
	  alert.present();
	}



    raise(param){
   	  const url = "search/addReport.json";
	    this.httpService.getData({
	      url: url,
	      body: {}
	    })
      .then(res=>{
				console.log(res);
				this.viewCtrl.dismiss();
      })
    }

    goSearch(){
   	const url = "search/customer.json";
   	const param={
   		"customName":this.reports.customer,
      	"pageNo":"1",
      	"dataCount":"10",
      	"fisrPageNo":"10",
      	"vague":true
   	}
    this.httpService.getData({
      url: url,
      body: param
    })
    .then(res=>{
      console.log(res);
      this.searchLists=res.customs;
      if(this.searchLists.length==0){
      	this.isSearch='none';
      }else{
      	this.isSearch='block';
      }
    })
   }
  goCustomer(cust){
  	this.reports.customer=cust.customName;
  	this.isSearch='none';
  }

  //获得当前位置信息
   getRoad() {
     //alert("hello");
     if (navigator.geolocation) {
       navigator.geolocation.getCurrentPosition(position =>
         {
           this.xLocation = position.coords.latitude;
           this.yLocation = position.coords.longitude;
         });
     }
     else {
       this.errLocation = "获取位置信息失败。";
     }
     //let urll = "http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location=39.983424,116.322987&output=json&pois=1&ak=oVF7rVxFBuiwNWwrqeYshKxme6kpO5ij";
     const url = "province/getprovince.json";
     this.httpService.getData({url:url}).then(res => {
       console.log(res);
       this.location = res.province[0] + res.city[0];
       //console.log("world");
     })
   }




}






