import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
import { RateDetailPage } from '../rateDetail/rateDetail';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ratepage',
  templateUrl: 'rate.html',
})
export class RatePage {
  rates:any={};
  isSearch:string;
  reports:any={};
  searchLists:any;

  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController,
  	private httpService: HttpService
  ) {}

  goSearch(){
    const url = "search/customer.json";
    // const param={
    //   "customName":this.reports.customer,
    //   "pageNo":"1",
    //   "dataCount":"10",
    //   "fisrPageNo":"10",
    //   "vague":true
    // }
    this.httpService.getData({
      url: url
      // body: param
    })
      .then(res=>{
        console.log(res);
        this.searchLists=res.customs;
        if(this.searchLists.length==0){
          this.isSearch='none';
        }else{
          this.isSearch='block';
        }
      })
  }
  goCustomer(cust){
    this.reports.customer=cust.customName;
    this.isSearch='none';
  }

   goRateDetail(){
   	console.log('去客户详情');
    const url = "search/rate.json";
	   	const param={
	   		"name":this.rates.name,


	      	"beginDate":this.rates.start,
	      	"endDate":this.rates.end,
	      	"pageNo":"1",
	      	"dataCount":"10",
	      	"firstPageNo":"10"
	   	}
	    this.httpService.getData({
	      url: url,
	      body: param
	    })
	    .then(res=>{
	      console.log(res);
		   this.navCtrl.push(RateDetailPage,{"ratesDetails":res},{ animate: true });
	    })


   }
	presentModal(){
		console.log("分享");
	}
}



