import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController  } from 'ionic-angular';
//import { SearchlistPage } from '../searchlist/searchlist';
import { CustomerPage } from '../customer/customer';
import { ProjectPage } from '../project/project';
import { RatePage } from '../rate/rate';
import { AccountSearchPage } from '../accountSearch/accountSearch';
import { BalancePage } from '../balance/balance';
/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-searchlistpage',
  templateUrl: 'searchlist.html',
})
export class SearchlistPage {
  
  
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public modalCtrl: ModalController
  ) {}

    goSearchList(style){
    	console.log(style);
		switch (style){
			case 'customer':
				this.navCtrl.push(CustomerPage,{},{ animate: true });
				break;
			case 'project':
				this.navCtrl.push(ProjectPage,{},{ animate: true });
				break;
			case 'rate':
				this.navCtrl.push(RatePage,{},{ animate: true });
				break;
			case 'detail':
				this.navCtrl.push(AccountSearchPage,{},{ animate: true });
				break;
			case 'balance':
				this.navCtrl.push(BalancePage,{},{ animate: true });
				break;
			default:
				break;
		}
    }

}



